<template>
    <div class="root">
  
      <!--  -->
      <el-row  :gutter="20">
        <el-col :span="6">
          <div>
            <span style="vertical-align: middle;"></span>
            <el-input
              placeholder=""
              style="display: inline-block; width: 60%; vertical-align: middle;"
              size="small"
              v-model="queryStudentParam.param.name"
              >
            </el-input>
          </div>
        </el-col>
        <el-col :span="6">
          <div>
            <span style="vertical-align: middle;"></span>
            <el-input
              placeholder=""
              style="display: inline-block; width: 60%; vertical-align: middle;"
              size="small"
              v-model="queryStudentParam.param.teacherName"
              >
            </el-input>
          </div>
        </el-col>
        <el-col :span="6">
          <div>
            <el-button type="primary" size="small" @click="getInfo"> </el-button>
            <el-button type="primary" plain size="small" @click="openDialog()"> </el-button>
          </div>
        </el-col>

        <el-col :span="6">
          <div style="padding-top: 8px;">
                <span style="vertical-align: middle;"> </span>
                <el-switch
                    v-model="courseSwitchStatus"
                    active-color="#13ce66"
                    inactive-color="#ff4949"
                    active-value="1"
                    inactive-value="0"
                    @change="handleSwitchChagne">
                </el-switch>
          </div>
        </el-col>
      </el-row>
  
      <!--  -->
      <el-table
        :data="tableData"
        border
        style="width: 100%"
        height="70vh"
        @selection-change="handleSelectionChange">
          <el-table-column
              prop="name"
              label=""
              width="125%">
          </el-table-column>
          <el-table-column
              prop="teacherName"
              label=""
              width="125%">
          </el-table-column>
          <el-table-column
              prop="credit"
              label=""
              width="125%">
          </el-table-column>
          <el-table-column
              prop="hour"
              label=""
              width="125%">
          </el-table-column>
          <el-table-column
              prop="time"
              label=""
              width="125%">
          </el-table-column>
          <el-table-column
              prop="placeName"
              label=""
              width="125%">
          </el-table-column>
          <el-table-column
              prop="courseStatusName"
              label=""
              width="125%">
          </el-table-column>
          <el-table-column
              fixed="right"
              label=""
              width="300%">
              <template slot-scope="scope"
              >
                <el-button
                  size="mini"
                  @click="getInfoById(scope.row)"
                  type="primary"
                  plain></el-button>
                  <el-button
                  size="mini"
                  type="warning"
                  plain
                  @click="endCourse(scope.row)"></el-button>
                  <el-button
                  size="mini"
                  type="danger"
                  @click="deleteCourse(scope.row)"
                  plain></el-button>
              </template>
          </el-table-column>
      </el-table>
  
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryStudentParam.currentPage"
        :page-sizes="[5,10, 15, 20, 25]"
        :page-size="queryStudentParam.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        style="margin-top: 14px;">
      </el-pagination>
  
      <!--  -->
      <el-dialog :title="title" :visible.sync="dialogFormAddVisible" style="width: 60%; margin: 0 auto; margin-top: -20px;">
       
        <el-form :model="form" size="small">
          <el-form-item label="" label-width="100px">
            <el-input v-model="form.name" autocomplete="off"  style="width: 250px;"></el-input>
          </el-form-item>
          <el-form-item label="" label-width="100px">
            <el-select v-model="form.teacherId" placeholder="" style="width: 250px;">
                <el-option v-for="item in teacher" :label="item.name" :value="item.teacherId" :key="item.teacherId"/>
            </el-select>
          </el-form-item>
          <el-form-item label="" label-width="100px">
            <el-select v-model="form.credit" placeholder="" style="width: 250px;">
                <el-option v-for="item in credits" :label="item" :value="item" :key="item"/>
            </el-select>
          </el-form-item>
          <el-form-item label="" label-width="100px">
            <el-input v-model="form.hour" autocomplete="off"  style="width: 250px;"></el-input>
          </el-form-item>
          <el-form-item label="" label-width="100px">
            <el-input v-model="form.time" autocomplete="off"  style="width: 250px;"></el-input>
          </el-form-item>
          <el-form-item label="" label-width="100px">
            <el-select v-model="form.placeId" placeholder="" style="width: 250px;">
                <el-option v-for="item in place" :label="item.placeName" :value="item.placeId" :key="item.placeId"/>
            </el-select>
          </el-form-item>
          <el-form-item label="" label-width="100px">
            <el-input type="textarea" v-model="form.description"  style="width: 250px; resize: none;"/>
            <!-- <textarea style="resize: none;width: 250px;height: 80px; border: 2px solid rgb(220,223,230);"  v-model="form.description"></textarea> -->
          </el-form-item>
        </el-form>

        <div slot="footer">
          <el-button @click="dialogFormAddVisible = false"> </el-button>
          <el-button type="primary" @click="insertInfo()"> </el-button>
        </div>

      </el-dialog>
  
      <!--  -->
      <el-dialog :title="title" :visible.sync="dialogFormEditionVisible" style="width: 60%; margin: 0 auto;">
        
        <el-form :model="form" size="small">
          <el-form-item label="" label-width="100px">
            <el-input v-model="form.name" autocomplete="off"  style="width: 250px;"></el-input>
          </el-form-item>
          <el-form-item label="" label-width="100px">
            <el-select v-model="form.teacherId" placeholder="" style="width: 250px;">
                <el-option v-for="item in teacher" :label="item.name" :value="item.teacherId" :key="item.teacherId"/>
            </el-select>
          </el-form-item>
          <el-form-item label="" label-width="100px">
            <el-select v-model="form.credit" placeholder="" style="width: 250px;">
                <el-option v-for="item in credits" :label="item" :value="item" :key="item"/>
            </el-select>
          </el-form-item>
          <el-form-item label="" label-width="100px">
            <el-input v-model="form.hour" autocomplete="off"  style="width: 250px;"></el-input>
          </el-form-item>
          <el-form-item label="" label-width="100px">
            <el-input v-model="form.time" autocomplete="off"  style="width: 250px;"></el-input>
          </el-form-item>
          <el-form-item label="" label-width="100px">
            <el-select v-model="form.placeId" placeholder="" style="width: 250px;">
                <el-option v-for="item in place" :label="item.placeName" :value="item.placeId" :key="item.placeId"/>
            </el-select>
          </el-form-item>
          <el-form-item label="" label-width="100px">
            <el-input type="textarea" v-model="form.description"  style="width: 250px; resize: none;"/>
            <!-- <textarea style="resize: none;width: 250px;height: 80px; border: 2px solid rgb(220,223,230);"  v-model="form.description"></textarea> -->
          </el-form-item>
        </el-form>

        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormEditionVisible = false"> </el-button>
          <el-button type="primary" @click="updateInfo()"> </el-button>
        </div>
      </el-dialog>
  
    </div>
  </template>
    
  <script>
    import { 
        getCourseByCondition,
        getAllPlace,
        insertANewCourse,
        updateCourse,
        deleteCourse,
        getCourseSwitchStatus,
        updateCourseStatus
    } from '../../api/course/course.js'
    import { getAllTeacher } from '../../api/member/teacher-manager.js'
    export default {
      data() {
        return {
            //swith
            courseSwitchStatus: '',
            //
            queryStudentParam: {
                pageSize: 10,                   //10
                currentPage: 1,                 //
                param: {
                    name: '',                   //
                    teacherName: ''             //
                }
            },
            selectedDataId: [],                 //id
            selectionStatus: true,              //,false,true
            dialogFormAddVisible: false,        //
            dialogFormEditionVisible: false,    //
            total: -1,                          //    
            tableData: [],                      // 
            //
            credits: ['1.0', '1.5', '2.0', '2.5', '3.0', '3.5', '4.0', '4.5', '5.0', '5.5', '6.0'],
            //
            title: '',
            form: {
                name: '',                       //
                teacherId: '',                  //id
                teacherName: '',                //
                credit: '',                     //
                hour: '',                       //
                time: '',                       //
                placeId: '',                    //id
                placeName: '',                  //
                description: '',                //
                courseStatusId: '5',            //id
                courseStatusName: ''         //
            },
            //
            place: [],
            //
            teacher: []
        }
      },
      created() {
        this.getCourseSwitchStatus()
        this.getInfo()
      },
      methods: {
        //
        async getInfo() {
          const result = await getCourseByCondition(this.queryStudentParam)
          this.tableData = result.data.data
          this.total = result.data.total
          console.log(result)
        },
        //
        async getCourseSwitchStatus() {
          const result = await getCourseSwitchStatus()
          if (result.data.code === 1) {
            this.courseSwitchStatus = result.data.data
          }
        },  
        //
        async getPlace() {
            const result = await getAllPlace()
            this.place = result.data.data
        },
        //
        async getTeacher() {
            const result = await getAllTeacher()
            this.teacher = result.data.data
        },
        //
        handleSizeChange(e) {
          this.queryStudentParam.pageSize = e
          this.getInfo()
        },
        //
        handleCurrentChange(e) {
          console.log(e)
          this.queryStudentParam.currentPage = e
          this.getInfo()
        },
        //
        async updateCourseStatus(courseSwitchStatus) {
          const result = await updateCourseStatus(courseSwitchStatus)
          console.log(result)
        },
        //
        handleSwitchChagne(e) {
          console.log(e)
          console.log(this.courseSwitchStatus)
          if (e === '1') {
            this.$confirm('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(async () => {   
                this.courseSwitchStatus = '1' 
                this.updateCourseStatus(this.courseSwitchStatus)  
                this.getInfo()
                this.$message({
                  type: 'success',
                  message: ''
                })
            }).catch(() => {
                this.$message({
                type: 'info',
                message: ''
                })   
                this.courseSwitchStatus = '0'     
            })
          }
          if (e === '0') {
            this.$confirm('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(async () => {   
                this.courseSwitchStatus = '0'  
                this.updateCourseStatus(this.courseSwitchStatus)  
                this.getInfo() 
                this.$message({
                  type: 'success',
                  message: ''
                })
            }).catch(() => {
                this.$message({
                type: 'info',
                message: ''
                })   
                this.courseSwitchStatus = '1'     
            })
          }
        },  
  
        //
        openDialog() {
            this.getPlace()
            this.getTeacher()
            this.title = ''
            this.form.studentNumber = '',
            this.form.name = '',
            this.form.studentClass = ''
            this.dialogFormAddVisible = true
        },
        // 
        async insertInfo() {
          const result = await insertANewCourse(this.form)
          console.log(result)
          this.dialogFormAddVisible = false
          this.$message({
              type: 'success',
              message: '!'
            })
          this.getInfo()
        },
  
        //id,
        async getInfoById(e) {
            if (e.courseStatusName !== '' && e.courseStatusName !== '') {
                this.$message({
                    type: 'warning',
                    message: '~'
                })
                return
            }
            console.log(e)
            this.title = ''
            this.form = e
            this.getPlace()
            this.getTeacher()
            this.dialogFormEditionVisible = true
        },
        //
        async updateInfo() {
            if (this.form.placeId == null || this.form.time == null) {
                this.$message({
                    type: 'warning',
                    message: ''
                })
                return
            }
            this.form.courseStatusId = '5',
            this.form.courseStatusName = ''
            const result = await updateCourse(this.form)
            console.log(result)
            this.dialogFormEditionVisible = false
            this.getInfo()
            this.$message({
                type: 'success',
                message: '!'
            })
        },
  
        //
        async endCourse(row) {
            if (row.courseStatusName !== '') {
                this.$message({
                    type: 'warning',
                    message: '~'
                })
                return
            }
            this.$confirm('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(async () => {   
                this.form = row
                this.form.courseStatusId = '3'
                const result = await updateCourse(this.form)
                console.log(result)
                this.$message({
                type: 'success',
                message: '!'
                })
                this.getInfo()
            }).catch(() => {
                this.$message({
                type: 'info',
                message: ''
                })        
            })
        },

        //
        deleteCourse(row) {
          console.log(row)
          if (row.courseStatusName !== '' && row.courseStatusName !== '') {
            this.$message({
                    type: 'warning',
                    message: ''
            })
            return
          }
          this.$confirm('', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(async () => {   
            const result = await deleteCourse(row)
            console.log(result)
            this.$message({
              type: 'success',
              message: '!'
            })
            this.getInfo()
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            })        
          })
        },
        
        //
        handleSelectionChange(e) {
          console.log(e)
          if (e.length > 0) {
            this.selectionStatus = false
          } else {
            this.selectionStatus = true
          }
          this.selectedDataId = []
          for (let i = 0; i < e.length; i++) {
            this.selectedDataId.push(e[i].studentId)
          }
          console.log(this.selectedDataId)
        },

      }
    }
  </script>
  
  <style scoped>
    .root {
      text-align: center;
    }
    .el-row {
      margin-bottom: 16px;
    }
  
  </style>