<template>
    <div>
        <el-table
        :data="tableData"
        border
        style="width: 100%"
        height="70vh"
        >
          <el-table-column
              prop="name"
              label=""
              width="130%">
          </el-table-column>
          <el-table-column
              prop="teacherName"
              label=""
              width="130%">
          </el-table-column>
          <el-table-column
              prop="credit"
              label=""
              width="130%">
          </el-table-column>
          <el-table-column
              prop="hour"
              label=""
              width="130%">
          </el-table-column>
          <el-table-column
              prop="time"
              label=""
              width="130%">
          </el-table-column>
          <el-table-column
              prop="placeName"
              label=""
              width="130%">
          </el-table-column>
          <el-table-column
              prop="courseStatusName"
              label=""
              width="130%">
          </el-table-column>
          <el-table-column
              prop="score"
              label=""
              width="130%">
          </el-table-column>
          <el-table-column
              fixed="right"
              label=""
              width="150%">
                <template slot-scope="scope">
                    <el-button
                    size="mini"
                    @click="handleExitCourse(scope.row)"
                    type="warning"
                    plain></el-button>
                </template>
          </el-table-column>
      </el-table>
    </div>
</template>


<script>
import { exitCourse, getAllSelectedCourse } from '@/api/course/course.js'
import { getUserInfo } from '@/utils/storage.js'
export default {
    data() {
        return  {
            studentId: '',    //id
            tableData: []
        }
    },
    created() {
        this.studentId = getUserInfo().id
        this.getAllSelectedCourse()
    },
    methods: {
        //id
        async getAllSelectedCourse() {
            const result = await getAllSelectedCourse(this.studentId)
            if (result.data.code === 1) {
                this.tableData = result.data.data
            }
        },
        //
        async exitCourse(row) {
            const form = {
                courseId: row.courseId,
                studentId: this.studentId
            }
            const result = await exitCourse(form)
            console.log(result)
        },

        //
        handleExitCourse(row) {
            if (row.courseStatusName === '' || row.courseStatusName === '') {
                this.$message({
                    type: 'warning',
                    message: ''
                }) 
                return
            } 
            this.$confirm('', '', {   
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(async () => {   
                this.exitCourse(row)
                this.$message({
                  type: 'success',
                  message: ''
                })
                this.getAllSelectedCourse()
            }).catch(() => {
                this.$message({
                type: 'info',
                message: ''
                })   
            })
        }


    }

}
</script>


