<template>
    <div class="root">
  
      <!--  -->
      <el-row  :gutter="20">
        <el-col :span="8">
          <div>
            <span style="vertical-align: middle;"></span>
            <el-input
              placeholder=""
              style="display: inline-block; width: 60%; vertical-align: middle;"
              size="small"
              v-model="queryStudentParam.param.name"
              >
            </el-input>
          </div>
        </el-col>
        <el-col :span="8">
          <div>
            <span style="vertical-align: middle;"></span>
            <el-input
              placeholder=""
              style="display: inline-block; width: 60%; vertical-align: middle;"
              size="small"
              v-model="queryStudentParam.param.teacherName"
              >
            </el-input>
          </div>
        </el-col>
        <el-col :span="8">
          <div>
            <el-button type="primary" size="small" @click="getInfo" style="width: 30%;"> </el-button>
          </div>
        </el-col>
      </el-row>
  
      <!--  -->
      <el-table
        :data="tableData"
        border
        style="width: 100%"
        height="70vh"
        >
            <el-table-column
                prop="name"
                label=""
                width="150%">
            </el-table-column>
            <el-table-column
                prop="teacherName"
                label=""
                width="150%">
            </el-table-column>
            <el-table-column
                prop="credit"
                label=""
                width="150%">
            </el-table-column>
            <el-table-column
                prop="hour"
                label=""
                width="150%">
            </el-table-column>
            <el-table-column
                prop="time"
                label=""
                width="150%">
            </el-table-column>
            <el-table-column
                prop="placeName"
                label=""
                width="170%">
            </el-table-column>
            <el-table-column
                fixed="right"
                label=""
                width="200%">
                <template slot-scope="scope"
                >
                <el-button
                    size="mini"
                    type="primary"
                    plain @click="openCourseDetail(scope.row)"></el-button>

                <el-button
                    size="mini"
                    @click="selectTheCourse(scope.row)"
                    type="primary"
                    ></el-button>

                <!-- <el-button
                    size="mini"
                    @click="exitTheCourse(scope.row)"
                    type="warning"
                   ></el-button> -->

                </template>
            </el-table-column>
        </el-table>
  
        <!--  -->
        <el-dialog :visible.sync="dialogFormVisible">
            <el-descriptions title="" :column="2" border>
                <el-descriptions-item label="">{{ detailCourseData.name }}</el-descriptions-item>
                <el-descriptions-item label="">{{ detailCourseData.teacherName }}</el-descriptions-item>
                <el-descriptions-item label="">{{ detailCourseData.credit }}</el-descriptions-item>
                <el-descriptions-item label="">{{ detailCourseData.hour }}</el-descriptions-item>
                <el-descriptions-item label="">{{ detailCourseData.time }}</el-descriptions-item>
                <el-descriptions-item label="">{{ detailCourseData.placeName }}</el-descriptions-item>
                <el-descriptions-item label="">{{ detailCourseData.description }}</el-descriptions-item>
            </el-descriptions>
        </el-dialog>



      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryStudentParam.currentPage"
        :page-sizes="[5,10, 15, 20, 25]"
        :page-size="queryStudentParam.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        style="margin-top: 14px;">
      </el-pagination>
  
    </div>
  </template>
    
  <script>
    import { getUserInfo } from '@/utils/storage'
    import { 
        getCourseByCondition,
        studentSelectCourse
        // judgeCourseSelectedStatus
        // exitCourse
    } from '../../api/course/course.js'
    export default {
      data() {
        return {
            //
            queryStudentParam: {
                pageSize: 10,                   //10
                currentPage: 1,                 //
                param: {
                    name: '',                   //
                    teacherName: '',            //
                    courseStatusId: '2'         //
                }
            },
            total: -1,                          //    
            tableData: [],                      // 
            dialogFormVisible: false,           //
            detailCourseData: {},               //
            //
            form: {
                courseId: '',
                studentId: ''
            }
        }
      },
      created() {
        const userInfo = getUserInfo()
        this.form.studentId = userInfo.id
        this.getInfo()
      },
      methods: {
        //
        async getInfo() {
          const result = await getCourseByCondition(this.queryStudentParam)
          this.tableData = result.data.data
          this.total = result.data.total
        },
        //
        handleSizeChange(e) {
          this.queryStudentParam.pageSize = e
          this.getInfo()
        },
        //
        handleCurrentChange(e) {
          console.log(e)
          this.queryStudentParam.currentPage = e
          this.getInfo()
        },

        //
        openCourseDetail(row) {
            this.detailCourseData = row
            this.dialogFormVisible = true
        },

        //
        selectTheCourse(row) {
            // console.log(row)
            this.$confirm('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(async () => {   
                this.form.courseId = row.courseId
                const result = await studentSelectCourse(this.form)
                if (result.data.code === 1) {
                    this.$message({
                        type: 'success',
                        message: ''
                    })
                } else {
                    this.$message({
                        type: 'warning',
                        message: ''
                    })
                }
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                })   
            })
        },

        //
        // async exitTheCourse(row) {
        //     //
        //     if (this.judgeCourseSelectedStatus(row.courseId) == false) {
        //         this.$message({
        //             type: "warning",
        //             message: "~"
        //         })
        //     }
        //     //
        //     else {
        //         this.$confirm('', '', {
        //             confirmButtonText: '',
        //             cancelButtonText: '',
        //             type: 'warning'
        //         }).then(async () => {   
        //             this.form.courseId = row.courseId
        //             const reuslt = await exitCourse(this.form)
        //             if (reuslt.data.code === 1) {
        //                 this.$message({
        //                         type: 'success',
        //                         message: ''
        //                 })
        //             }
        //         }).catch(() => {
        //             this.$message({
        //                 type: 'info',
        //                 message: ''
        //             })   
        //         })
        //     }
        // },

        // //
        // async judgeCourseSelectedStatus(couseId) {
        //     let form = {
        //         couseId,
        //         studentId: '24'
        //     }
        //     const result = await judgeCourseSelectedStatus(form)
        //     console.log(result)
        //     return result.data.code == 1
        // }

      }
    }
  </script>
  
  <style scoped>
    .root {
      text-align: center;
    }
    .el-row {
      margin-bottom: 16px;
    }
  </style>