<!--  -->
<template>
    <div>
        <CourseDetailHeader>
          <template slot="title">{{ courseInfo.name }}</template>
          <template slot="tag">
            <el-tag
                type="warning"
                size="mini"
                style="margin-left: 8px;"
              >
                {{ courseInfo.courseStatusName }}
            </el-tag>
          </template>
          <template slot="time">{{ courseInfo.time }}</template>
          <template slot="placeName">{{ courseInfo.placeName }}</template>
          <template slot="description">{{ courseInfo.description }}</template>
        </CourseDetailHeader>
        <div>
            <el-tabs>
                <el-tab-pane label="">
                    <el-table
                    :data="student"
                    height="50vh"
                    style="width: 100%;">
                        <el-table-column
                            prop="name"
                            label=""
                            width="220%">
                        </el-table-column>
                        <el-table-column
                            prop="studentNumber"
                            label=""
                            width="220%">
                        </el-table-column>
                        <el-table-column
                            prop="studentClass"
                            label=""
                            width="220%">
                        </el-table-column>
                        <el-table-column
                            prop="score"
                            label=""
                            width="220%">
                        </el-table-column>
                        <el-table-column
                        fixed="right"
                        label=""
                        width="200">
                        <template slot-scope="scope">
                            <el-button type="text" size="small" @click="setScore(scope.row)"></el-button>
                        </template>
                        </el-table-column>
                    </el-table>
                </el-tab-pane>
            </el-tabs>
        </div>


        <!--  -->
        <el-dialog title="" :visible.sync="dialogFormVisible" style="width: 60%; margin: 0 auto;">
          <el-form :model="form">
            <el-form-item label="">
              <el-input v-model="form.score" autocomplete="off"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false"> </el-button>
            <el-button type="primary" @click="updateStudentScore()"> </el-button>
          </div>
      </el-dialog>

    </div>
</template>

<script>
import CourseDetailHeader from '@/components/CourseDetailHeader.vue'
import { getSelectTheCourseStudents, getCourseById, updateStudentScore } from '@/api/course/course.js'
export default {
    data() {
      return {
        dialogFormVisible: false, //
        courseId: '',//id
        courseInfo: {}, //
        student: [],
        form: {
          score: '',  //
          courseId: '',
          studentId: ''
        }
      }
    },
    components: {
        CourseDetailHeader
    },
    created() {
      this.getCourseById()
      this.getSelectTheCourseStudents()
    },
    methods: {
      //
      async getSelectTheCourseStudents() {
         const result = await getSelectTheCourseStudents(this.courseId)
         if (result.data.code === 1) {
          this.student = result.data.data
         }
      },
      //id
      async getCourseById() {
        this.courseId = this.$route.query.courseId
        const result = await getCourseById(this.$route.query.courseId)
        console.log(result)
        if (result.data.code === 1) {
          this.courseInfo = result.data.data
        }
      },

      //
      setScore(row) {
        if (this.courseInfo.courseStatusName != '') {
          this.$message({
            type: 'warning',
            message: ''
          })
          return
        }
        this.form.courseId = this.courseInfo.courseId
        this.form.studentId = row.studentId
        this.dialogFormVisible = true
      },

      //
      async updateStudentScore() {
        await updateStudentScore(this.form)
        this.dialogFormVisible = false
        this.getSelectTheCourseStudents()
      }

    }
}
</script>

