<!--  -->
<template>
    <div>
        <div class="root" @click="openDialog()"> 
            <!--  -->
            <div style="margin-left: 20px;">
                <el-avatar src="https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg"></el-avatar>
            </div>
            <div style="margin-left: 10px;">
                <div  class="userName-font">
                    <slot name="userName"></slot>
                </div>
                <div class="application-font">
                    <slot name="operation"></slot> "<slot name="course"></slot>"
                </div>
            </div>  
            <div class="right">
                <slot name = "courseExaminationName">
                    <el-tag type="warning" size="mini" class="tag">
                        
                    </el-tag>
                </slot>
                <div class="date-font">
                <slot name="dateTime">2023-01-05 12:12:25</slot>
                </div>
            </div>  
        </div>

        <!--  -->
        <el-dialog :visible.sync="dialogFormVisible">
            <el-descriptions :title="title" :column="2" border>
                <el-descriptions-item label="">{{ newData.courseName }}</el-descriptions-item>
                <el-descriptions-item label="">{{ newData.teacherName }}</el-descriptions-item>
                <el-descriptions-item label="">{{ newData.courseCredit }}</el-descriptions-item>
                <el-descriptions-item label="">{{ newData.courseHour }}</el-descriptions-item>
                <el-descriptions-item label="">{{ newData.courseTime }}</el-descriptions-item>
                <el-descriptions-item label="">{{ newData.coursePlaceName }}</el-descriptions-item>
                <el-descriptions-item label="">{{ newData.courseDescription }}</el-descriptions-item>
            </el-descriptions>

            <el-descriptions title="" column="2" border style="margin-top: 20px;" v-if="newData.operationName === ''">
                <el-descriptions-item label="">{{ originalData.name }}</el-descriptions-item>
                <el-descriptions-item label="">{{ originalData.teacherName }}</el-descriptions-item>
                <el-descriptions-item label="">{{ originalData.credit }}</el-descriptions-item>
                <el-descriptions-item label="">{{ originalData.hour }}</el-descriptions-item>
                <el-descriptions-item label="">{{ originalData.time }}</el-descriptions-item>
                <el-descriptions-item label="">{{ originalData.placeName }}</el-descriptions-item>
                <el-descriptions-item label="">{{ originalData.description }}</el-descriptions-item>
            </el-descriptions>

            <div slot="footer" class="dialog-footer" v-if="isShowButton === 'true'">
                <el-button @click="agreeOrNot('')"></el-button>
                <el-button type="primary" @click="agreeOrNot('')"> </el-button>
            </div>

        </el-dialog>

    </div>
</template>

<script>
import { getCourseAppliactionById } from '@/api/course-application/course-application.js'
import { examineACourse } from '@/api/course-examination/course-examination.js'
import { getCourseById } from '@/api/course/course.js'
export default {
    data() {
        return {
            dialogFormVisible: false,
            //
            title: '',
            //
            newData: {},
            //
            originalData: {}
        }
    },
    props: {
        //id
        courseApplicationId: {
            type: String
        },
        //
        isShowButton: {
            type: String
        }
    },
    methods: {
        async getCourseInfoById() {
            const res = await getCourseById(this.newData.courseId)
            if (res.data.code === 1) {
                this.originalData = res.data.data
            }
        },
        async openDialog() {
            //id
            const result = await getCourseAppliactionById(this.courseApplicationId)
            this.newData = result.data.data
            const operationName = result.data.data.operationName
            console.log(this.isShowButton)
            if (this.isShowButton === 'true') {
                switch(operationName) {
                    case '':
                        this.title = ''
                        break
                    case '':
                        this.title = ''
                        this.getCourseInfoById()
                        break
                    case '':
                        this.title = ''
                        break
                    default:
                        break
                }
            }
            this.dialogFormVisible = true
        },
        //
        async agreeOrNot(name) {
            this.newData.courseExaminationName = name
            await examineACourse(this.newData)
            this.dialogFormVisible = false
            this.$emit('refresh')//
            this.$message({
                type: 'success',
                message: ''
            })
        }
    }
}
</script>

<style scoped>
    .root {
        position: relative;
        display: flex;
        margin-bottom: 12px;
        width: 100%;
        height: 80px;
        background-color: rgba(250, 249, 249, 0.978);
        border-radius: 7px;
        cursor: pointer;
    }
    .root > div {
        align-self: center;
    }
    .userName-font {
        font-size: small;
        color: rgba(0, 0, 0, 0.695);
        margin-bottom: 2%;
    }
    .application-font {
        font-size: medium;
        font-weight: 550;
        color: rgba(0, 0, 0, 0.77);
    }
    .right {
        position: absolute;
        right: 20px;
        text-align: right;
        right: 26px;
    }
    .tag {
        text-align: right;
        margin-bottom: 26%;
    }
    .date-font {
        font-size:x-small;
        color: rgba(0, 0, 0, 0.626);
    }
</style>