<template>
  <div class="root">

    <!--  -->
    <el-row  :gutter="20">
      <el-col :span="6">
        <div>
          <span style="vertical-align: middle;"></span>
          <el-input
            placeholder=""
            style="display: inline-block; width: 60%; vertical-align: middle;"
            size="small"
            v-model="queryTeacherParam.param.teacherNumber"
            >
          </el-input>
        </div>
      </el-col>
      <el-col :span="6">
        <div>
          <span style="vertical-align: middle;"></span>
          <el-input
            placeholder=""
            style="display: inline-block; width: 60%; vertical-align: middle;"
            size="small"
            v-model="queryTeacherParam.param.name"
            >
          </el-input>
        </div>
      </el-col>
      <el-col :span="6">
        <div>
          <span style="vertical-align: middle;"></span>
          <el-select v-model="queryTeacherParam.param.departmentName" filterable placeholder="" size="small" style="width: 60%;">
            <el-option
              v-for="item in departmentOptions"
              :key="item.departmentId"
              :label="item.departmentName"
              :value="item.departmentName"
              >
            </el-option>
          </el-select>
        </div>
      </el-col>
      <el-col :span="6">
        <div>
          <el-button type="primary" size="small" @click="getInfo"> </el-button>
          <el-button type="primary" size="small" @click="openDialog()"> </el-button>
          <el-button type="danger" @click="deleteSelectedData()" plain :disabled ="selectionStatus" size="small"> </el-button>
        </div>
      </el-col>
    </el-row>

    <!--  -->
    <el-table
      :data="tableData"
      border
      style="width: 100%"
      height="70vh"
      @selection-change="handleSelectionChange">
        <el-table-column
          type="selection"
          width="55">
        </el-table-column>
        <el-table-column
            prop="teacherNumber"
            label=""
            width="200%">
        </el-table-column>
        <el-table-column
            prop="name"
            label=""
            width="200%">
        </el-table-column>
        <el-table-column
            prop="departmentName"
            label=""
            width="200%">
        </el-table-column>
        <el-table-column
            prop="dateTime"
            label=""
            width="300%">
        </el-table-column>
        <el-table-column
            fixed="right"
            label="">
            <template slot-scope="scope">
            <el-button
              size="mini"
              @click="getInfoById(scope.row)"></el-button>
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.row)"></el-button>
          </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="queryTeacherParam.currentPage"
      :page-sizes="[5,10, 15, 20, 25]"
      :page-size="queryTeacherParam.pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
      style="margin-top: 14px;">
    </el-pagination>

    <!--  -->
    <el-dialog :title="title" :visible.sync="dialogFormAddVisible" style="width: 50%; margin: 0 auto;">
      <el-form :model="form">
        <el-form-item label="" label-width="60px">
          <el-input v-model="form.teacherNumber" autocomplete="off"  style="width: 250px;"></el-input>
        </el-form-item>
        <el-form-item label="" label-width="60px">
          <el-input v-model="form.name" autocomplete="off"  style="width: 250px;"></el-input>
        </el-form-item>
        <el-form-item label="" label-width="60px">
          <el-select v-model="form.departmentName" filterable placeholder="" style="width: 250px;">
            <el-option
              v-for="item in departmentOptions"
              :key="item.departmentId"
              :label="item.departmentName"
              :value="item.departmentName"
              >
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormAddVisible = false"> </el-button>
        <el-button type="primary" @click="insertInfo()"> </el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog :title="title" :visible.sync="dialogFormEditionVisible" style="width: 50%; margin: 0 auto;">
      <el-form :model="form">
        <el-form-item label="" label-width="60px">
          <el-input v-model="form.teacherNumber" autocomplete="off"  style="width: 250px;"></el-input>
        </el-form-item>
        <el-form-item label="" label-width="60px">
          <el-input v-model="form.name" autocomplete="off"  style="width: 250px;"></el-input>
        </el-form-item>
        <el-form-item label="" label-width="60px">
          <el-select v-model="form.departmentName" filterable placeholder="" style="width: 250px;">
            <el-option
              v-for="item in departmentOptions"
              :key="item.departmentId"
              :label="item.departmentName"
              :value="item.departmentName"
              >
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormEditionVisible = false"> </el-button>
        <el-button type="primary" @click="updateInfo()"> </el-button>
      </div>
    </el-dialog>

  </div>
</template>
  
<script>
  import { 
    getDepartmentInfo,
    getTeachersInfo, 
    insertTeacherInfo, 
    deleteTeacherInfo,
    getTeacherInfoById,
    updateTeacherInfoById 
  } from '../../api/member/teacher-manager.js'
  export default {
    data() {
      return {
        //
        queryTeacherParam: {
          pageSize: 10,                   //10
          currentPage: 1,                 //
          param: {
            teacherNumber: '',            //
            name: '',                     //
            departmentId: '',             //id
            departmentName: ''            //
          }
        },
        selectedDataId: [],               //id
        selectionStatus: true,            //,false,true
        dialogFormAddVisible: false,      //
        dialogFormEditionVisible: false,  //
        total: -1,                        //    
        tableData: [],                    // 
        //
        title: '',
        form: {
          teacherId: '',                  //id
          teacherNumber: '',              //
          name: '',                       //
          departmentId: '',               //id
          departmentName: ''              //
        },
        //
        departmentOptions: []
      }
    },
    created() {
      this.getInfo()
    },
    methods: {
      //
      async getInfo() {
        const result = await getTeachersInfo(this.queryTeacherParam)
        this.tableData = result.data.data
        this.total = result.data.total
        const info = await getDepartmentInfo()
        this.departmentOptions = info.data.data
        this.synDepartmentIdAndName(1)
        console.log(this,this.queryTeacherParam)
        console.log(result)
        console.log(info)
      },

      //id
      synDepartmentIdAndName(state) {
        //
        if (state === 1) {
          for (let i = 0; i < this.departmentOptions.length; i++) {
            if (this.departmentOptions[i].departmentName === this.queryTeacherParam.param.departmentName) {
              this.queryTeacherParam.param.departmentId = this.departmentOptions[i].departmentId
              break
            }
          }
        }
        //
        if (state === 0) {
          for (let i = 0; i < this.departmentOptions.length; i++) {
            if (this.departmentOptions[i].departmentName === this.form.departmentName) {
              this.form.departmentId = this.departmentOptions[i].departmentId
              break
            }
          }
        }
      },

      //
      handleSizeChange(e) {
        this.queryTeacherParam.pageSize = e
        this.getInfo()
      },
      //
      handleCurrentChange(e) {
        console.log(e)
        this.queryTeacherParam.currentPage = e
        this.getInfo()
      },

      //
      openDialog() {
        this.title = ''
        this.form.teacherNumber = '',
        this.form.name = '',
        this.form.departmentId = '',
        this.form.departmentName = '',
        this.dialogFormAddVisible = true
      },
      // 
      async insertInfo() {
        this.synDepartmentIdAndName(0)
        const result = await insertTeacherInfo(this.form)
        console.log(result)
        this.dialogFormAddVisible = false
        this.$message({
            type: 'success',
            message: '!'
          })
        this.getInfo()
      },

      //id,
      async getInfoById(e) {
        const result = await getTeacherInfoById(e.teacherId)
        console.log(result)
        this.title = ''
        this.form.teacherId = result.data.data.teacherId
        this.form.teacherNumber = result.data.data.teacherNumber
        this.form.name = result.data.data.name
        this.form.departmentId = result.data.data.departmentId
        this.form.departmentName = result.data.data.departmentName
        this.dialogFormEditionVisible = true
      },
      //
      async updateInfo() {
        this.synDepartmentIdAndName(0)
        const result = await updateTeacherInfoById(this.form)
        console.log(result)
        this.dialogFormEditionVisible = false
        this.getInfo()
        this.$message({
            type: 'success',
            message: '!'
        })
      },

      //
      handleDelete(row) {
        console.log(row)
        this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(async () => {   
          const result = await deleteTeacherInfo(row.teacherId)
          console.log(result)
          this.$message({
            type: 'success',
            message: '!'
          })
          this.getInfo()
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })        
        })
      },
      
      //
      handleSelectionChange(e) {
        console.log(e)
        if (e.length > 0) {
          this.selectionStatus = false
        } else {
          this.selectionStatus = true
        }
        this.selectedDataId = []
        for (let i = 0; i < e.length; i++) {
          this.selectedDataId.push(e[i].teacherId)
        }
        console.log(this.selectedDataId)
      },
      //
      deleteSelectedData() {
        this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(async () => {
          const result = await deleteTeacherInfo(this.selectedDataId)
          console.log(result)
          this.$message({
            type: 'success',
            message: '!'
          })
          this.getInfo()
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })        
        })
      }
    }
  }
</script>

<style scoped>
  .root {
    text-align: center;
  }
  .el-row {
    margin-bottom: 16px;
  }
</style>