<template>
  <div class="root">

    <!--  -->
    <el-row  :gutter="20">
      <el-col :span="6">
        <div>
          <span style="vertical-align: middle;"></span>
          <el-input
            placeholder=""
            style="display: inline-block; width: 60%; vertical-align: middle;"
            size="small"
            v-model="queryStudentParam.param.studentNumber"
            >
          </el-input>
        </div>
      </el-col>
      <el-col :span="6">
        <div>
          <span style="vertical-align: middle;"></span>
          <el-input
            placeholder=""
            style="display: inline-block; width: 60%; vertical-align: middle;"
            size="small"
            v-model="queryStudentParam.param.name"
            >
          </el-input>
        </div>
      </el-col>
      <el-col :span="6">
        <div>
          <span style="vertical-align: middle;"></span>
          <el-input
            placeholder=""
            style="display: inline-block; width: 60%; vertical-align: middle;"
            size="small"
            v-model="queryStudentParam.param.studentClass"
            >
          </el-input>
        </div>
      </el-col>
      <el-col :span="6">
        <div>
          <el-button type="primary" size="small" @click="getInfo"> </el-button>
          <el-button type="primary" size="small" @click="openDialog()"> </el-button>
          <el-button type="danger" @click="deleteSelectedData()" plain :disabled ="selectionStatus" size="small"> </el-button>
        </div>
      </el-col>
    </el-row>

    <!--  -->
    <el-table
      :data="tableData"
      border
      style="width: 100%"
      height="70vh"
      @selection-change="handleSelectionChange">
        <el-table-column
          type="selection"
          width="55">
        </el-table-column>
        <el-table-column
            prop="studentNumber"
            label=""
            width="200%">
        </el-table-column>
        <el-table-column
            prop="name"
            label=""
            width="200%">
        </el-table-column>
        <el-table-column
            prop="studentClass"
            label=""
            width="200%">
        </el-table-column>
        <el-table-column
            prop="dateTime"
            label=""
            width="300%">
        </el-table-column>
        <el-table-column
            fixed="right"
            label="">
            <template slot-scope="scope">
            <el-button
              size="mini"
              @click="getInfoById(scope.row)"></el-button>
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.row)"></el-button>
          </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="queryStudentParam.currentPage"
      :page-sizes="[5,10, 15, 20, 25]"
      :page-size="queryStudentParam.pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
      style="margin-top: 14px;">
    </el-pagination>

    <!--  -->
    <el-dialog :title="title" :visible.sync="dialogFormAddVisible" style="width: 50%; margin: 0 auto;">
      <el-form :model="form">
        <el-form-item label="" label-width="60px">
          <el-input v-model="form.studentNumber" autocomplete="off"  style="width: 250px;"></el-input>
        </el-form-item>
        <el-form-item label="" label-width="60px">
          <el-input v-model="form.name" autocomplete="off"  style="width: 250px;"></el-input>
        </el-form-item>
        <el-form-item label="" label-width="60px">
          <el-input v-model="form.studentClass" autocomplete="off" style="width: 250px;"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormAddVisible = false"> </el-button>
        <el-button type="primary" @click="insertInfo()"> </el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog :title="title" :visible.sync="dialogFormEditionVisible" style="width: 50%; margin: 0 auto;">
      <el-form :model="form">
        <el-form-item label="" label-width="60px">
          <el-input v-model="form.studentNumber" autocomplete="off"  style="width: 250px;"></el-input>
        </el-form-item>
        <el-form-item label="" label-width="60px">
          <el-input v-model="form.name" autocomplete="off"  style="width: 250px;"></el-input>
        </el-form-item>
        <el-form-item label="" label-width="60px">
          <el-input v-model="form.studentClass" autocomplete="off" style="width: 250px;"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormEditionVisible = false"> </el-button>
        <el-button type="primary" @click="updateInfo()"> </el-button>
      </div>
    </el-dialog>

  </div>
</template>
  
<script>
  import { 
    getStudentsInfo, 
    insertStudentInfo, 
    deleteStudentInfo,
    getStudentInfoById,
    updateStudentInfoById 
  } from '../../api/member/stduent-manager.js'
  export default {
    data() {
      return {
        //
        queryStudentParam: {
          pageSize: 10,                   //10
          currentPage: 1,                 //
          param: {
            studentNumber: '',            //
            name: '',                     //
            studentClass: ''              //
          }
        },
        selectedDataId: [],               //id
        selectionStatus: true,            //,false,true
        dialogFormAddVisible: false,      //
        dialogFormEditionVisible: false,  //
        total: -1,                        //    
        tableData: [],                    // 
        //
        title: '',
        form: {
          studentId: '',                  //id
          studentNumber: '',              //
          name: '',                       //
          studentClass: ''                //
        }
      }
    },
    created() {
      this.getInfo()
    },
    methods: {
      //
      async getInfo() {
        const result = await getStudentsInfo(this.queryStudentParam)
        this.tableData = result.data.data
        this.total = result.data.total
        console.log(result)
      },

      //
      handleSizeChange(e) {
        this.queryStudentParam.pageSize = e
        this.getInfo()
      },
      //
      handleCurrentChange(e) {
        console.log(e)
        this.queryStudentParam.currentPage = e
        this.getInfo()
      },

      //
      openDialog() {
        this.title = ''
        this.form.studentNumber = '',
        this.form.name = '',
        this.form.studentClass = ''
        this.dialogFormAddVisible = true
      },
      // 
      async insertInfo() {
        const result = await insertStudentInfo(this.form)
        console.log(result)
        this.dialogFormAddVisible = false
        this.$message({
            type: 'success',
            message: '!'
          })
        this.getInfo()
      },

      //id,
      async getInfoById(e) {
        const result = await getStudentInfoById(e.studentId)
        console.log(result)
        this.title = ''
        this.form.studentId = result.data.data.studentId
        this.form.studentNumber = result.data.data.studentNumber
        this.form.name = result.data.data.name
        this.form.studentClass = result.data.data.studentClass
        this.dialogFormEditionVisible = true
      },
      //
      async updateInfo() {
        const result = await updateStudentInfoById(this.form)
        console.log(result)
        this.dialogFormEditionVisible = false
        this.getInfo()
        this.$message({
            type: 'success',
            message: '!'
        })
      },

      //
      handleDelete(row) {
        console.log(row)
        this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(async () => {   
          const result = await deleteStudentInfo(row.studentId)
          console.log(result)
          this.$message({
            type: 'success',
            message: '!'
          })
          this.getInfo()
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })        
        })
      },
      
      //
      handleSelectionChange(e) {
        console.log(e)
        if (e.length > 0) {
          this.selectionStatus = false
        } else {
          this.selectionStatus = true
        }
        this.selectedDataId = []
        for (let i = 0; i < e.length; i++) {
          this.selectedDataId.push(e[i].studentId)
        }
        console.log(this.selectedDataId)
      },
      //
      deleteSelectedData() {
        this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(async () => {
          const result = await deleteStudentInfo(this.selectedDataId)
          console.log(result)
          this.$message({
            type: 'success',
            message: '!'
          })
          this.getInfo()
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })        
        })
      }
    }
  }
</script>

<style scoped>
  .root {
    text-align: center;
  }
  .el-row {
    margin-bottom: 16px;
  }

</style>