<!--  -->
<template>
    <div class="root">
        <el-form ref="form" :model="form" label-width="100px">
            <el-form-item label="">
                <el-input v-model="form.courseName" placeholder=""/>
            </el-form-item>
            <el-form-item label="">
                <el-select v-model="form.courseCredit" placeholder="" style="width: 100%;">
                    <el-option v-for="item in credits" :label="item" :value="item" :key="item"/>
                </el-select>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="form.courseHour" placeholder=""/>
            </el-form-item>
            <el-form-item label="">
                <el-input type="textarea" v-model="form.courseDescription"/>
            </el-form-item>
            <el-form-item style="text-align: center;">
                <el-button type="primary" @click="onSubmit" style="width: 140px; position: relative;right: 20px;"> </el-button>
                <!-- <el-button> </el-button> -->
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
import { applyANewCourse } from '@/api/course-application/course-application.js'
import { getUserInfo } from '@/utils/storage.js'
export default {
    data() {
      return {
        //
        credits: ['1.0', '1.5', '2.0', '2.5', '3.0', '3.5', '4.0', '4.5', '5.0', '5.5', '6.0'],
        //
        form: {
            teacherId: '',                      //id
            courseName: '',                     //
            courseCredit: '',                   //
            courseHour: '',                     //
            courseDescription: '',              //  
            operationName: '',              //
            courseExaminationName: ''   
        }
      }
    },
    created() {
        this.form.teacherId = getUserInfo().id
    },  
    methods: {
        
        async submitInfo() {
            const result = await applyANewCourse(this.form)
            console.log(result)
        },
        onSubmit() {
            this.$confirm('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(async () => {
                this.submitInfo()
                this.form.courseName = '',
                this.form.courseCredit = '',
                this.form.courseHour = '',
                this.form.courseDescription = ''
                this.$message({
                    type: 'success',
                    message: '!'
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                })        
            })
        }
    }
}
</script>

<style scoped>
    .root {
        width: 60vh;
    }
</style>